
.ratingfilter-wrapper .select {
  padding: 18px 0;
  margin: 0 18px;
  font-size: 0;
  position: relative;
}
.ratingfilter-wrapper .select:after {
  content: ' ';
  display: block;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.ratingfilter-wrapper .select > span {
  display: inline-block;
  padding: 8px 12px;
  font-size: 12px;
  line-height: 16px;
  color: #4d555d;
  margin-right: 8px;
  border-radius: 2px;
}
.ratingfilter-wrapper .select > span span {
  font-size: 8px;
}
.ratingfilter-wrapper .select > span.positive,
.ratingfilter-wrapper .select > span.all {
  background-color: rgba(0,160,220,0.2);
}
.ratingfilter-wrapper .select > span.positive.active,
.ratingfilter-wrapper .select > span.all.active {
  color: #fff;
  background-color: #00a0dc;
}
.ratingfilter-wrapper .select > span.negative {
  background-color: rgba(77,85,93,0.2);
}
.ratingfilter-wrapper .select > span.negative.active {
  color: #fff;
  background-color: #4d555d;
}
.ratingfilter-wrapper .only-content {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
  font-size: 0;
}
.ratingfilter-wrapper .only-content.on .icon-check_circle {
  color: #00c850;
}
.ratingfilter-wrapper .only-content .icon-check_circle {
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
  font-size: 24px;
}
.ratingfilter-wrapper .only-content .text {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
}


