
#app {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
}
.nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  width: 100%;
  height: 40px;
  -ms-flex-preferred-size: 40px;
      flex-basis: 40px;
  line-height: 40px;
  position: relative;
}
.nav:after {
  content: ' ';
  display: block;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.nav .nav-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.nav .nav-item > a {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
.nav .nav-item > a.active {
  color: #f01414;
}
.content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}


