
.count {
  font-size: 0;
}
.count .minus,
.count .add {
  display: inline-block;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}
.count .minus .inner {
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.count .minus.move-enter-active,
.count .minus.move-leave-active {
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.count .minus.move-enter,
.count .minus.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.count .minus.move-enter .inner,
.count .minus.move-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.count .num {
  display: inline-block;
  width: 24px;
  text-align: center;
  vertical-align: top;
  font-size: 10px;
  line-height: 24px;
  color: #93999f;
}


