
.shopcart {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 48px;
  z-index: 50;
  background-color: #141d27;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.shopcart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0;
  background-color: #141d27;
}
.shopcart .content .content-left .logo-wrapper {
  display: inline-block;
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  padding: 6px;
  margin: 0 12px;
  border-radius: 50%;
  background-color: #141d27;
