  bottom: 8px;
  text-align: center;
}
.shopcart .content .content-left .logo-wrapper .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #2b343c;
  color: #80858a;
}
.shopcart .content .content-left .logo-wrapper .logo.highlight {
  background: #00a0dc;
  color: #fff;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
  line-height: 44px;
  font-size: 24px;
}
.shopcart .content .content-left .logo-wrapper .total-num {
  position: absolute;
