  font-size: 9px;
  font-weight: 700;
  color: #fff;
  line-height: 16px;
}
.shopcart .content .content-left .price {
  display: inline-block;
  padding-right: 12px;
  border-right: 1px solid rgba(255,255,255,0.1);
  vertical-align: top;
  margin-top: 12px;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  color: rgba(255,255,255,0.4);
