  height: 48px;
  line-height: 48px;
  text-align: center;
  font-size: 12px;
  color: rgba(255,255,255,0.4);
  font-weight: 700;
  background: #2b333b;
}
.shopcart .content .content-right.enough {
  background-color: #00b43c;
  color: #fff;
}
.shopcart .balls-container .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  -webkit-transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.shopcart .balls-container .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.shopcart .cart-list {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.shopcart .cart-list.slide-enter,
.shopcart .cart-list.slide-leave-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.shopcart .cart-list.slide-enter-active,
.shopcart .cart-list.slide-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.shopcart .cart-list .list-head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 18px;
  line-height: 40px;
  background-color: #f3f5f7;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.shopcart .cart-list .list-head .text-shop {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #07111b;
  font-size: 14px;
  font-weight: 200;
}
.shopcart .cart-list .list-head .text-clear {
  font-size: 12px;
  color: #00a0dc;
}
.shopcart .cart-list .list-content {
  max-height: 217px;
  background-color: #fff;
  padding: 0 18px;
  overflow: hidden;
}
.shopcart .cart-list .list-content .cart-item {
  display: -webkit-box;
  display: -ms-flexbox;
