}
.shopcart .cart-list .list-content .cart-item .food-price {
  display: inline-block;
  margin: 0 12px 0 18px;
  color: #f01414;
  font-weight: 700;
}
.shopcart .cart-list .list-content .cart-item .count-wrapper {
