}
.shopcart .mask {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -50;
  background-color: rgba(7,17,27,0.6);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.shopcart .mask.fade-enter,
.shopcart .mask.fade-leave-active {
  opacity: 0;
}
.shopcart .mask.fade-enter-active,
.shopcart .mask.fade-leave-active {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}


